.chatBox{
    background-color: #444444;
    border-radius: 15px;
    width: 25vw;
    height: 85vh;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 2;
    overflow: hidden;
}
.chatBoxFooter{
    /*send to bottom*/
    margin-top: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.chatBoxFooterContent{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    /*fix issue where element goes past the flex container*/
    width: 100%;
}

.chatBoxFooterMessage{
    width: 100%;
    height: 100%;
    z-index: 1;
    margin-right: 40px;
}

.chatBoxFooterMessage input[type=text]{
    
}
.chatBoxBody{
    height: 100%;
    overflow-y: scroll;

}

.chatBoxBodyMessages{
    height: 100%;
    overflow-y: scroll;
}
.chatBoxBodyMessages{
    height: 100%;
}

.chat-avatar{
    width: 200px;
    height: 200px;
    align-self: flex-start;
    position: relative;
    z-index: 0;
    top: 10px;
    /*slightly blur to reduce pixelation*/

}

.chatBoxHeaderContent {
    /*send to far right*/
    margin-left: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.chatBoxBodyMessage{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 10px;
    max-width: 50%;
    padding: 15px;
    border-radius: 15px;
}

.ai-message{
    background-color: #2c2a2a;
    margin-left: auto;
    margin-left: 0;
}

.user-message{
    background-color: #4e6a6a;
    margin-left: auto;
    margin-right: 0;
}